@import "./NavBar";
@import "./variables";
@import "./Home";
@import "./Skills";
@import "./Projects";
@import "./Contact";
@import "./Footer";
@import "./Slider";
@import "./Project";
@import "./BuiltWith";

//prefix default styles
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  background: none repeat scroll 0 0 transparent;
  border: 0 none;
  font-size: 100%;
  margin: 0;
  outline: 0 none;
  padding: 0;
  vertical-align: baseline;
}
html {
  max-width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}
body {
  max-width: 100%;
  overflow-x: hidden;
  margin: 0;
  display: flex;
  justify-content: center;

  font-family: Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(230, 230, 230);
  background: rgb(0, 0, 0);
}

.App {
  width: 100%;
  max-width: 1600px;
}

.navlink {
  position: absolute;
  top: -$navbarHeight + 2rem;
}

.navlink-t2 {
  top: -$navbarHeight - 5rem;
}

// avoid default link styles
a {
  color: inherit;
  text-decoration: inherit;
}

p {
  color: rgb(181, 181, 181);
}

//prefix for form styling
input,
textarea,
select,
option {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
ol,
ul,
li {
  list-style: none outside none;
}

section {
  max-width: 100vw;
  margin: 0 auto;
}

.wrapper {
  width: 100vw;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
}
